
input[type="text"], textarea {
  font: inherit !important;
  color: inherit !important;
}
.full-width {
  margin-left: -15px;
  margin-right: -15px;
}
.link:hover {
  text-decoration: underline;
}
.active-text {
   color: #7aa5e0;
   cursor: pointer;
}
.active-text:active {
  color: #3487c2;
}
.faded-text {
  color: #a6a4a4;
}
.highlighted-text {
  color: #fe7862;
}
.rpm-button {
  border-radius: 100px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  width: 100%;
  border: 1px solid #e3e3e3;
  padding: 8px;
  position: relative;
  cursor: default;
  overflow: hidden;
}
.diagram-options {
  position: absolute;
  top: 30px;
  left: 30px;
  line-height: 22px;
}
@-webkit-keyframes spinner {
0% {-webkit-transform: rotate(0);transform: rotate(0);
}
100% {-webkit-transform: rotate(90deg);transform: rotate(90deg);
}
}
@keyframes spinner {
0% {-webkit-transform: rotate(0);transform: rotate(0);
}
100% {-webkit-transform: rotate(90deg);transform: rotate(90deg);
}
}
@-webkit-keyframes spinner-color {
0% {-webkit-transform: rotate(0);transform: rotate(0); border-color: #3de192;
}
25% {-webkit-transform: rotate(90deg);transform: rotate(90deg); border-color: #71c9f7;
}
50% {-webkit-transform: rotate(180deg);transform: rotate(180deg); border-color: #fbd04f;
}
75% {-webkit-transform: rotate(270deg);transform: rotate(270deg); border-color: #f8865e;
}
100% {-webkit-transform: rotate(360deg);transform: rotate(360deg); border-color: #3de192;
}
}
@keyframes spinner-color {
0% {-webkit-transform: rotate(0);transform: rotate(0); border-color: #3de192;
}
25% {-webkit-transform: rotate(90deg);transform: rotate(90deg); border-color: #71c9f7;
}
50% {-webkit-transform: rotate(180deg);transform: rotate(180deg); border-color: #fbd04f;
}
75% {-webkit-transform: rotate(270deg);transform: rotate(270deg); border-color: #f8865e;
}
100% {-webkit-transform: rotate(360deg);transform: rotate(360deg); border-color: #3de192;
}
}
.spinner {
  position: absolute;
  left: 50%;
  top: 50%;
}
.spinner:before {
  content: '';
  position: absolute;
  display: block;
  width: 18px;
  height: 18px;
  left: -9px;
  top: -9px;
  border-radius: 3px;
  -webkit-animation: spinner-color 2.5s ease infinite;
          animation: spinner-color 2.5s ease infinite;
  border: 3px solid #e5e5e5 !important;
  padding: 0 !important;
  margin: 0 !important;
  opacity: 0.5;
}
.info {
  background: rgb(253,244,233);
  background: linear-gradient(135deg, rgba(253,244,233,1) 0%,rgba(255,242,225,1) 100%);
  padding: 13px 14px;
  border-radius: 6px;
}
.no-transition-enter-active, .no-transition-leave-active {
}
.no-transition-enter, .no-transition-leave-to /* .fade-leave-active below version 2.1.8 */ {
  display: none;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .3s, -webkit-transform 0.3s;
  transition: opacity .3s, -webkit-transform 0.3s;
  transition: opacity .3s, transform 0.3s;
  transition: opacity .3s, transform 0.3s, -webkit-transform 0.3s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.fade-long-enter-active, .fade-long-leave-active {
  -webkit-transition: opacity .5s, -webkit-transform 0.3s;
  transition: opacity .5s, -webkit-transform 0.3s;
  transition: opacity .5s, transform 0.3s;
  transition: opacity .5s, transform 0.3s, -webkit-transform 0.3s;
}
.fade-long-enter, .fade-long-leave-to /* .fade-long-leave-active below version 2.1.8 */ {
  opacity: 0;
}
.slide-h-enter-active, .slide-h-leave-active {
  -webkit-transition: opacity .3s ease, -webkit-transform .3s ease;
  transition: opacity .3s ease, -webkit-transform .3s ease;
  transition: opacity .3s ease, transform .3s ease;
  transition: opacity .3s ease, transform .3s ease, -webkit-transform .3s ease;
}
.slide-h-enter, .slide-h-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  -webkit-transform: translate(30px, 0px);
  transform: translate(30px, 0px);
}
.slide-v-enter-active, .slide-v-leave-active {
  -webkit-transition: opacity .6s ease, -webkit-transform .6s ease;
  transition: opacity .6s ease, -webkit-transform .6s ease;
  transition: opacity .6s ease, transform .6s ease;
  transition: opacity .6s ease, transform .6s ease, -webkit-transform .6s ease;
}
.slide-v-enter, .slide-v-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
  -webkit-transform: translate(0px, 60px);
  transform: translate(0px, 60px);
}
.sidebar .full-width {
  margin: 0 -15px;
}
.info-buttons-wrapper {
  text-align: left;
  color: #c2c2c2;
  display: table;
  table-layout: fixed;
  width: 100%;
  border: 0;
}
.info-buttons-wrapper > * {
  cursor: pointer;
  display: table-cell;
  vertical-align: middle;
  text-align: center;
  padding: 8px 0;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #f5f5f5;
  -webkit-transition: background-color 0.2s, color 0.3s, border 0.2s;
  transition: background-color 0.2s, color 0.3s, border 0.2s;
}
.info-buttons-wrapper > *:hover {
  background-color: #f5f5f5;
}
.info-buttons-wrapper > *:active {
  background-color: #ebebeb;
  border-color: #ebebeb;
}
.info-buttons-wrapper * {
  vertical-align: middle;
}
.info-buttons-wrapper .icon {
  display: inline-block;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  position: relative;
  margin-right: 6px;
  background: #fff;
  display: none;
}
.info-buttons-wrapper .icon:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  border-radius: 100%;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  opacity: 0.7;
}
.buttons-zone:hover .info-button .icon:before {
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.info-buttons-wrapper .edit-icon:before {
  background: #58aff3;
}
.info-buttons-wrapper .cancel-icon:before {
  background: #58aff3;
}
.info-buttons-wrapper .remove-icon:before {
  background: #fc746e;
}
.info-buttons-wrapper .icon .bar {
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
}
.info-buttons-wrapper .icon,
.info-buttons-wrapper .icon:before,
.info-buttons-wrapper .icon .bar:before,
.info-buttons-wrapper .icon .bar:after {
  -webkit-transition: background-color 0.2s, border 0.2s, opacity 0.2s, -webkit-transform 0.2s;
  transition: background-color 0.2s, border 0.2s, opacity 0.2s, -webkit-transform 0.2s;
  transition: transform 0.2s, background-color 0.2s, border 0.2s, opacity 0.2s;
  transition: transform 0.2s, background-color 0.2s, border 0.2s, opacity 0.2s, -webkit-transform 0.2s;
}
.info-buttons-wrapper .icon .bar:before, .info-buttons-wrapper .icon .bar:after {
  opacity: 0;
}
.info-button:hover .bar:before, .info-button:hover .bar:after {
  opacity: 1;
}
.info-buttons-wrapper .edit-icon .bar {
  -webkit-transform: rotate(40deg);
          transform: rotate(40deg);
}
.info-buttons-wrapper .cancel-icon .bar {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.info-buttons-wrapper .cancel-icon .bar:before {
  left: 2px;
  right: 2px;
  height: 3px;
  top: 50%;
  margin-top: -1.5px;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
}
.info-buttons-wrapper .cancel-icon .bar:after {
  top: 2px;
  bottom: 2px;
  width: 3px;
  left: 50%;
  margin-left: -1.5px;
  -webkit-transform: translate(0, 100%);
          transform: translate(0, 100%);
}
.cancel-button:hover .cancel-icon .bar:before {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.cancel-button:hover .cancel-icon .bar:after {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
}
.edit-button:hover .edit-icon .bar:before {
  -webkit-transform: translate(0, 50%);
          transform: translate(0, 50%);
}
.remove-button:hover .remove-icon .bar:before {
  -webkit-transform: translate(3px, 0);
          transform: translate(3px, 0);
}
.info-buttons-wrapper .edit-button {
  border-bottom: 2px solid #abd8ff;
  color: #3897da;
}
.info-buttons-wrapper .remove-button {
  border-bottom: 2px solid #ffa8a3;
  color: #e15e5e;
}
.info-buttons-wrapper .cancel-button {
  border-bottom: 2px solid #abd8ff;
  color: #3897da;
}
.buttons-zone .remove-button.disabled {
  text-decoration: line-through;
}
.sidebar textarea {
  border: 0;
  outline: 0;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: #f2f2f2;
  -webkit-transition: background-color 0.25s ease, -webkit-box-shadow 0.4s ease;
  transition: background-color 0.25s ease, -webkit-box-shadow 0.4s ease;
  transition: background-color 0.25s ease, box-shadow 0.4s ease;
  transition: background-color 0.25s ease, box-shadow 0.4s ease, -webkit-box-shadow 0.4s ease;
  padding: 8px;
}
.sidebar textarea:focus {
    background: #fff;
    -webkit-box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
            box-shadow: 0 0 8px rgba(0, 0, 0, 0.2);
}
.sidebar-padding {
  padding: 15px;
}



